
import { CustomLocation, LocationLatLng } from "./customLocation.class";
import { LeafletMap } from "./leafletMap.class";
import { OpenStreetData } from "./openStreetData.class";

import { debounceTime } from 'rxjs';
import { rubrics } from './rubrics';
 

class TableTennisMap {

  private currentStatus = 'tabletennis';
  public myPosition: LocationLatLng
  public myMap: LeafletMap;

  private setStatusTo = async (modus: string) => {
    console.log(' set status to: ', modus)
    this.currentStatus = modus;
    this.myMap.onChangeModus(modus);
    // todo trigger mapsStatus change
  }

  public initMap = async (): Promise<void> => {

    this.myPosition = await CustomLocation.getCurrentPosition();
  
    this.myMap = new LeafletMap("map", this.myPosition, "tabletennis");
    
    this.afterInitMap();
    //this.myMap.mapInitialized(this.afterInitMap);
    /*
    this.myMap.digimap.on('load', () => {
      console.log("load hu 1 call afterinitmap");

      //this.afterInitMap();
    })
*/
  }

  private afterInitMap = () => {
    console.log("hu 2 call afterinitmap");

    this.myMap.mapsStatus.pipe(
      debounceTime(300)
    ).subscribe(async status => {

      const tableCoords: LocationLatLng[] = await OpenStreetData.getCoordinatesForModus(status.center, status.zoom, status.modus);
  
      tableCoords.forEach(table => {
        const markerType = rubrics[this.currentStatus];
        this.myMap.addMarker(table, markerType);
      })
  

      const menuul = document.getElementById("menulist");
  
    
      removeAllChildNodes(menuul);
    
       status.modus
      for (let rubric in rubrics) {
        console.log(' rubric ', rubric, rubrics[rubric].text);
        var li = document.createElement("li");
        li.addEventListener('click', () => this.setStatusTo(rubric))
        li.appendChild(document.createTextNode(rubrics[rubric].text));
        if (status.modus == rubric) {
          li.style.backgroundColor = rubrics[rubric].color;
          li.style.color = 'white';

        }

        menuul?.appendChild(li);
    
      }

    });




  }
}


function removeAllChildNodes(parent) {
  while (parent.firstChild) {
      parent.removeChild(parent.firstChild);
  }
}

const  mainfunc =async () => {
  
  const tableTennis = new TableTennisMap();



  await tableTennis.initMap();
  

} 


mainfunc();



declare global {
  interface Window {
    setStatusTo: (status: string) => void;
  }
}

