import { LocationLatLng } from "./customLocation.class";
import { ReplaySubject} from 'rxjs';
import { CustomMap, MapStatus, MarkerType} from "./customMap.class";

import { map,  tileLayer, MapOptions, Map, marker, MarkerOptions , Icon} from "leaflet";

import 'leaflet/dist/leaflet.css';






export class LeafletMap implements CustomMap {


    public mapsStatus: ReplaySubject<MapStatus>;
    public digimap: Map;
    private modus: string = "tabletennis";

    constructor(mapDiv: string, location: LocationLatLng, modus: string) {

        this.modus = modus;
        const options: MapOptions = {
            center: location,
            zoom: 17,
        };
        this.digimap = map(mapDiv, options);
        tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
            maxZoom: 19,
            minZoom: 14,
            attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
        }).addTo(this.digimap);

        
        this.mapsStatus = new ReplaySubject(1);

        this.digimap.on('zoomend', (event) => {
            console.log('zoomed  ')

            this.mapsStatus.next({
                center: this.digimap.getCenter(),
                zoom: this.digimap.getZoom(),
                modus: this.modus
            });
        });

        this.digimap.on('moveend', (event) => {
            console.log('moved ')
            this.mapsStatus.next({
                center: this.digimap.getCenter(),
                zoom: this.digimap.getZoom(),
                modus: this.modus
            });
        })

    }

    /*
    mapInitialized = (callback) => {
        this.myMap.on('load', callback)
    }
    */

    onChangeModus(modus: string) {
        this.modus = modus;
        if (this.digimap) {
            this.mapsStatus.next({
                center: this.digimap.getCenter(),
                zoom: this.digimap.getZoom(),
                modus: this.modus
            });

        }


    }
 

    addMarker(location: LocationLatLng, markerType: MarkerType): void  {
        if (this.digimap) {
            console.log("add marker")

      
            const osmMakerOpt: MarkerOptions = {
                title: markerType.text,
                icon: new Icon({iconUrl: `http://maps.google.com/mapfiles/ms/icons/${markerType.color}-dot.png`})
            };
                

            const osmMarker =  marker(location, osmMakerOpt);
            osmMarker.addTo(this.digimap);

        }
    }
}