import axios from 'axios';
import { LocationLatLng } from "./customLocation.class";
import { rubrics } from './rubrics';

export class OpenStreetData {

    static getCoordinatesForModus = async (center: LocationLatLng, zoom: number , modus: string): Promise<LocationLatLng[]> => {

        const currentMode = rubrics[modus];
        const url = "https://overpass-api.de/api/interpreter";
        const internalZoom = 1 / zoom * 30000;
        const data = `
            [out:json][timeout:25];
            (
                node["${currentMode.osmKey}"="${currentMode.osmValue}"](around:${internalZoom}, ${center.lat}, ${center.lng});
            );
            out body;
            >;
            out skel qt;
        `
        const response = await axios.post(url, data);

        if (response.data) {
            return response.data.elements.map(item => {
                return {
                    lat: item.lat,
                    lng: item.lon,
                }
            })
        }
        return [];


    }

}